  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.page__body {
  min-height: 320px;
  position: relative;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  transition: transform .3s;
  position: fixed;
  inset: 0;
  overflow-y: auto;
  transform: translateX(-100%);
}

.page:has(.page__menu:target) .page__menu {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

